import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgxEditorModule } from 'ngx-editor';

import { NewProfessorFormComponent } from './new-professor-form.component';
import { ProfessorService } from '../../angular-services/professor.service';
import { TeamService } from '../../angular-services/team.service';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    RouterModule,
    NgxEditorModule
  ],
  declarations: [
    NewProfessorFormComponent
  ],
  exports: [
    NewProfessorFormComponent
  ],
  providers: [
    ProfessorService,
    TeamService
  ]
})
export class NewProfessorFormModule { }
