import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { ProfessorService } from '../../angular-services/professor.service';
import { TeamService } from '../../angular-services/team.service';
import { Team } from '../../classes/team';

@Component({
  selector: 'app-new-professor-form-admin',
  templateUrl: './new-professor-form.component.html',
  styleUrls: ['./new-professor-form.component.css']
})
export class NewProfessorFormComponent implements OnInit {

  formNewProfessor: FormGroup;
  teams: Team[] = [];
  alert: any = { type: '', message: '', show: false };

  constructor(private professorService: ProfessorService, private teamService: TeamService) { }

  ngOnInit() {
    this.initForm();
    this.getTeams();
  }

  initForm() {
    this.formNewProfessor = new FormGroup({
      name: new FormControl('', Validators.required),
      description: new FormControl('', Validators.required),
      idTeam: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewProfessor.valid) {
      let data = this.formNewProfessor.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.professorService.addProfessor(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getTeams() {
    this.teamService.getTeams().subscribe(
      response => {
        if (response !== undefined) {
          for (let team of response) {
            if (team.status) {
              this.teams.push(team);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao carregar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

}
